export const fader =
	trigger('routeAnimations', [
		transition('* <=> *', [
			query(':enter, :leave', [
				style({
					opacity: 0,
				}),
			], {optional: true}),
			query(':enter', [
				animate('600ms ease',
					style({ opacity: 1})
				),
			], {optional: true})
		]),
	]);
