import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GlobalService } from '../global.service';

@Component({
  selector: 'app-option-buttons',
  templateUrl: './option-buttons.component.html',
  styleUrls: ['./option-buttons.component.css']
})
export class OptionButtonsComponent implements OnInit {

  constructor(private route: ActivatedRoute, private globalService: GlobalService) { }

  id = '';

  obterPaginaPorId() {
        return this.globalService.obterPaginaPorId(this.id);
        //console.log(this.id);
  }


  obterYes() {

    let yes = this.obterPaginaPorId().button1;

    //console.log(yes);
    return yes;
  }

  obterNo() {

    let no = this.obterPaginaPorId().button2;

    //console.log(no);
    return no;
  }

  ///

  obterNextYes() {

    let nextyes = this.obterPaginaPorId().nbuttonid1;

    //console.log(nextyes);
    return nextyes;
  }

  obterNextNo() {

    let nextno = this.obterPaginaPorId().nbuttonid2;

    //console.log(nextno);
    return nextno;
  }





  ngOnInit() {
    this.route.paramMap.subscribe(params => {
        this.id = params.get('id');
      });
  }

}
