import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GlobalService } from '../global.service';

@Component({
  selector: 'app-top-icon',
  templateUrl: './top-icon.component.html',
  styleUrls: ['./top-icon.component.css']
})
export class TopIconComponent implements OnInit {

  constructor(private route: ActivatedRoute, private globalService: GlobalService) { }

  id = '';
  
  obterPaginaPorId() {
        return this.globalService.obterPaginaPorId(this.id);
        //console.log(this.id);
  }


  obterTopIcon() {

    let topicon = this.obterPaginaPorId().topicon;

    //console.log(topicon);
    return topicon;
  }





  ngOnInit() {
  this.route.paramMap.subscribe(params => {
      this.id = params.get('id');
    });
  }

}
