import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GlobalService } from '../global.service';
@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {

  constructor(private route: ActivatedRoute, private globalService: GlobalService) { }

  id = '';
  
  obterPaginaPorId() {
        return this.globalService.obterPaginaPorId(this.id);
        //console.log(this.id);
  }


  obterPreviousPage() {

    let previouspage = this.obterPaginaPorId().previouspage;

    //console.log(text);
    return previouspage;
  }


  message: string = "hamburguer";

  @Output() messageEvent = new EventEmitter<string>();

  sendMessage() {
    this.messageEvent.emit(this.message);
    //console.log(this.message);
  }


  ngOnInit() {
  this.route.paramMap.subscribe(params => {
      this.id = params.get('id');
    });
  }

}
